.title {
    font-family: "American Scribe";
}

.promotions {
    background-color: papayawhip;
    height: auto;
}

.promotion1 {
    background-color: aquamarine;
    padding: 20px;
    height: auto;
    width: 800px;
    border: 1px solid aquamarine;
    border-radius: 10px;
    position: relative;
    left: 18%;
    margin-bottom: 30px;
}

/* Extra small devices (phones, 768px and down) */
@media only screen and (max-width: 768px) {
    .promotions {
        width: 100vw;
    }

    .promotion1 {
        width: 80vw;
        left: 2%;
    }
}

/* Medium devices (769px to 1000px) */
@media only screen and (min-width: 769px) and (max-width: 1000px) {
    .promotions {
        height: 100vh;
    }

    .promotion1 {
        width: 80vw;
        left: 6%;
    }
}